@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #F7FD04;
}


header{
    display: flex;
    background-color: #F98404;
    align-items: center;
    color: #dbdbdb;
}

.card{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 10px;
    margin-top: 10px;
}

.card-head{
    display: flex;
    justify-content: space-between;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    padding: 10px;
    border: black solid;
    background-color: #FC5404;
}

.card-content{
    padding: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: white;  
    border: black solid;  
}

.card-head__delete{
    border-radius: 10px;
    color: white;
    background-color: black;
}

.new-expense__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 100%;
}
.new-expense__control select{
  padding: 10px;
  width: 10rem;
  border-radius: 10px;
  margin-bottom: 0.5rem;
}
.new-expense__actions-btn {
  border-radius: 10px;
  background-color: #fc5404;
  font-size: larger;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  align-self: flex-end;
}

textarea {
  border-radius: 10px;
  padding: 10px;
  font-size: larger;
  max-width: 260px;
}

form {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;
  border-radius: 10px;
  border: black solid;
  background-color: #f9b208;
  padding: 20px;
  margin: 10px;
}


.contain{
    display: flex;
    margin: 50px;
    padding: 10px;
    border-radius: 10px;
    justify-content: space-around;
    flex-wrap: wrap;
    background-color:#F9B208;
}
